
import Photo from './Photo';
import React, { Component } from 'react';
export default ( {list_data} ) => {
    // console.log("here is: ", list_data);
    return (
      <div className="photo-gallery">
				{
			list_data.map((photo)  =>  {
			return(
            <div key={photo.id} className="photo-item"> 
			<Photo {...photo} /> 
			</div> 
					)
				})
         }
    </div>
	)}




