import React, { Component, PropTypes } from 'react';
export default class Search extends Component {
    constructor(props) {
        super(props);
    }
    update_list(event) {
        //console.log('update_list')
        const { list_data , on_filter } = this.props;
        let txt = event.target.value;
        let filtered_list = list_data.filter( item =>
            item.title.toLowerCase().includes(txt.toLowerCase()) 
        )
        on_filter(filtered_list)
    }
    render() {
        return <input type="text" className="filter" placeholder={this.props.placeholderText} onChange={(e) => this.update_list(e)} />
    }

}
