import React, { Component } from 'react';
import fetch from 'isomorphic-fetch';
import List from './List';
import Photo from './Photo'; 
import Search from './Search'; 
import TopBar from './TopBar'; 

export default class App extends Component {
    constructor(props){
        super(props);
        this.state = {
        original_list:[],
        displayed_list:[],
        placeholder:'search for images...',
        }
    }

    componentWillMount(){
		fetch('https://api.flickr.com/services/rest/?method=flickr.photos.search&safe_search=1&format=json&nojsoncallback=1&api_key=bac9f1ccfd854f27894fd47c4f01b1e8&content_type=1&is_getty=1')
            .then( response => {
                if(response.ok) {
                    return response.json();
                }
                throw new Error('Network response was not ok.');
            })
            .then( data => {
                let photos = data.photos.photo;
                //  console.table(photos);
                // console.log(`${data.length} items loaded`);
                 this.setState({
                     original_list: photos,
                     displayed_list: photos
                });
            })
            .catch( error => {
                console.error(`fetch operation failed: ${error.message}`);
            });
}
   update_list_state(filtered_list){
        // console.log('update_list_state',filtered_list.length)
        this.setState({
            original_list : this.state.original_list,
            displayed_list: filtered_list
        })
   }
    render() {
        return (
        <div className="app">
            <TopBar>React.js</TopBar>
            <div className="header">
                    <h1>Image Gallery with Flickr API</h1>
                <div className="filter-container">
                <h4 className="filter_title">{this.state.displayed_list.length}items displayed</h4>
                <Search className="filter" placeholderText={this.state.placeholder}
              list_data={this.state.original_list} 
              on_filter={(fl)=> this.update_list_state(fl)}/>
              </div>
            </div>
              <List list_data={this.state.displayed_list}/>
                 
        </div>
        )}
}


