import React from 'react';

export default ( {image_url, altTag} ) => {
    return (
        <div className="photo-box">
              <a target="_blank" href={image_url}>
		      <img className="thumb" src={image_url} alt={altTag}></img>
              </a>
	    </div>
    )
}


